<template><section><html><head></head><body><h1>SimplePolylineGeometry</h1>
<p>The <code v-pre="">vc-geometry-polyline-simple</code> component is used to a polyline modeled as a line strip; the first two positions define a line segment, and each additional position defines a line segment from the previous position. Need to be used as a subcomponent of <code v-pre="">vc-instance-geometry</code>. Can be mounted to <code v-pre="">vc-primitive</code>.</p>
<h2>Example</h2>
<h3>Load SimplePolylineGeometry with <code v-pre="">vc-geometry-polyline-simple</code></h3>
<h4>preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive :appearance="appearance" :geometry-instances.sync="geometryInstances">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-polyline-simple ref="polyline" :positions="positions"></vc-geometry-polyline-simple>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
