<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> 	An array of Cartesian3 defining the positions in the polyline as a line strip. <strong>Structure: [{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>colors</td>
<td>Array</td>
<td></td>
<td><code v-pre="">optional</code> An Array of Color defining the per vertex or per segment colors.</td>
</tr>
<tr>
<td>colorsPerVertex</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> A boolean that determines whether the colors will be flat across each segment of the line or interpolated across the vertices.</td>
</tr>
<tr>
<td>arcType</td>
<td>Number</td>
<td><code v-pre="">1</code></td>
<td><code v-pre="">optional</code> The type of line the polyline segments must follow. <strong>NONE: 0, GEODESIC: 1, RHUMB: 2</strong></td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The distance, in radians, between each latitude and longitude if options.arcType is not ArcType.NONE. Determines the number of positions in the buffer.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The ellipsoid to be used as a reference.</td>
</tr>
</tbody>
</table>
<hr>
<p>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/SimplePolylineGeometry.html">SimplePolylineGeometry</a></strong></p>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometry: null,
          geometryInstances: null,
          positions: [
            { lng: 102.1, lat: 29.5 },
            { lng: 106.2, lat: 29.5 },
            { lng: 106.2, lat: 33.5 },
            { lng: 108.2, lat: 35.5 },
            { lng: 102.1, lat: 33.5 }
          ],
          attributes: null
        }
      },
      mounted() {
        this.$refs.polyline.createPromise.then(({ Cesium, viewer, cesiumObject }) => {
          const boundingSphere = Cesium.BoundingSphere.fromPoints(cesiumObject._positions)
          viewer.scene.camera.flyToBoundingSphere(boundingSphere)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          const { PerInstanceColorAppearance, ColorGeometryInstanceAttribute } = Cesium
          this.appearance = new PerInstanceColorAppearance({
            flat: true
          })
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random())
          }
        }
      }
    }
  </script>